header {
    @include color($background-color: $color-brand);
    line-height: 2.5;
    position: sticky;
    top: 0px;
    z-index: get-z-index(header);

    @include media(tablet) {
        line-height: 5;
    }
}

.header__container {
    @include row();
    align-items: stretch;
    flex-wrap: wrap;

    @include media(tablet) {
        flex-wrap: nowrap;
    }
}

.header__logo {
    font-size: 1.5em;
    font-weight: bolder;
    font-family: $font-logo;
    text-align: center;

    @include span-columns($tablet: 2, $mobile: 9);
}

.header__logo__link {
    @include link-style($color: inherit, $hover-decoration: none, $display: inline-block);
    height: 100%;
}

.header__hamburger {
    color: inherit;
    background-color: inherit;
    border: none;
    cursor: pointer;
    display: block;
    padding: 0;
    
    @include span-columns($tablet: 1 ,$mobile: 3);
    @include media(desktop) {
        display: none;
    }

}

